import '../style/bootstrap-popover-x.min.css';
import Profile from "../components/ProfilePageComponents/Profile";
import { setClose } from '../redux/actions/advCreator';
import { connect } from 'react-redux';

const mapStateToProps = (state, ownProps) => {
    return {
        ...ownProps,
        open: state.advReducer.open
    }
}

const mapDispatchToProps = dispatch => {
    return {
        setClose: () => dispatch(setClose())
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Profile);