import React from "react";
import {Badge, Carousel, Col, Image, Row} from "react-bootstrap";
import * as Icon from "react-feather";
import {capitalize} from "../../helpers/capitalize";
import hash from 'object-hash';

const MainInfoRow = ({person}) => {
    const {
        names,
        images,
        user_ids,
        urls, gender,
        origin_countries
    } = person;
    return (
        <Row className={"mx-2"}>
            <Col lg={{order: 2, span: 3}} className={"text-lg-left text-center"}>
                <Carousel className={"mx-auto"} indicators={false} id={"profileImage"} >
                    {
                        images && images.length
                        ? 
                            (
                                images.map(image => (
                                    <Carousel.Item key={hash(image)}>
                                        <Image
                                            fluid
                                            roundedCircle
                                            className={"mb-3 profile-pic"}
                                            src={`http://thumb.pipl.com/image?height=150&width=150&tokens=${image.thumbnail_token}`}/>
                                    </Carousel.Item>
                                ))
                            )
                        : null
                    }
                </Carousel>
            </Col>
            <Col lg={{order: 1, span: 9}}>
                <div className="mb-5">
                    <Row>
                        {
                            names.length && user_ids.length
                            ?
                                (
                                    <Col lg={8}>
                                        <h1 className="h3 text-lg-left text-center">{names[0].display} ({user_ids[0].content})</h1>
                                    </Col>
                                )
                            :null
                        }
                    </Row>
                    {
                        gender
                        ?
                            (
                                <div className="text-secondary text-lg-left text-center">
                                    {capitalize(gender.content)}
                                </div>
                            )
                        : null
                    }
                    {
                        origin_countries && origin_countries.length
                        ?
                            (
                                <div className="text-secondary text-lg-left text-center">
                                    From {origin_countries[0].country}
                                </div>
                            )
                        : null
                    }
                </div>
            </Col>
        </Row>
    );
}

export default MainInfoRow;