import {AUTH} from "../actions/authCreators";

const initialState = {
    loggedIn: false,
    fetching: false,
    error: null,
    data: {}
}

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case AUTH.LOGIN:
            const { data } = action.payload;
            return {
                ...state,
                loggedIn: true,
                fetching: false,
                data,
                error: null
            };
        case AUTH.LOGOUT:
            return {
                ...state,
                loggedIn: false,
                session_token: null
            };
        case AUTH.FETCH_STARTED:
            return {
                ...state,
                fetching: true
            };
        case AUTH.FETCH_FAILED:
            return {
                ...state,
                error: action.payload.error,
                fetching: false
            }
        case AUTH.LOGOUT_FAILED:
        default:
            return state;
    }
}

export default authReducer;