import React, {Component} from "react";
import {Col, Container, Row} from "react-bootstrap";
import ResultsSidebar from "../components/ResultsSidebar/ResultsSidebar";
import {connect} from "react-redux";
import Profile from "../pages/ProfilePage";
import Results from "../components/Results/Results";
import {clearPersons, isFetching} from "../redux/actions/personCreators";
import ReactToPrint, {PrintContextConsumer} from "react-to-print";
import Preloader from "../components/Preloader/Preloader";

class ResultsPage extends Component {

    componentWillUnmount() {
        const { clearPersons } = this.props;
        clearPersons();
    }

    render() {
        const { person, persons, searchQuery, isFetching, setIsFetching, unsetIsFetching } = this.props;
        console.log(person);
        return (
            <main className="py-4 h-100" ref={el => (this.componentRef = el)}>
                <Container className={"h-100"}>
                    <Row className={"px-3 py-2 h-100"}>
                        <>
                            <ReactToPrint content={() => this.componentRef} onBeforeGetContent={() => setIsFetching()} onBeforePrint={() => unsetIsFetching()}>
                                <PrintContextConsumer>
                                    {({ handlePrint }) => (
                                        <ResultsSidebar handleClick={handlePrint} searchQuery={searchQuery} />
                                    )}
                                </PrintContextConsumer>
                            </ReactToPrint>
                            {
                                !isFetching
                                    ?
                                        (
                                            <Col md={9}>
                                                {
                                                    Object.keys(person).length || Object.keys(persons).length
                                                        ?
                                                        (
                                                            Object.keys(person).length
                                                                ? <Profile person={person} persons={persons} />
                                                                : <Results persons={persons} />
                                                        )
                                                        :
                                                        (
                                                            <Col md={9} className={"d-flex align-items-center justify-content-center"}>
                                                                <h1>No person found</h1>
                                                            </Col>
                                                        )
                                                }
                                            </Col>
                                        )
                                    :
                                        (
                                            <Col md={9}>
                                                <Preloader />
                                            </Col>
                                        )
                            }
                        </>
                    </Row>
                </Container>
            </main>
        );
    }
}

const mapStateToProps = state => {
    return {
        person: state.personReducer.person,
        persons: state.personReducer.possible_persons,
        searchQuery: state.personReducer.searchQuery,
        errorHandle: state.personReducer.errorHandle,
        isFetching: state.personReducer.isFetching
    }
}

const mapDispatchToProps = dispatch => {
    return {
        clearPersons: () => dispatch(clearPersons()),
        setIsFetching: () => dispatch(isFetching(true)),
        unsetIsFetching: () => dispatch(isFetching(false))
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(ResultsPage);