import React from 'react';
import {QueryRowItem} from './QueryRowItem';

const initialData = {
	addresses: ["display"],
	emails: ["address"],
	names: ["display"],
	jobs: ["display"],
	educations: ["display"],
	phones:["display"],
	usernames: ["content"]
};
const QueryRow = ({searchQuery}) => {
    return (
        Object.keys(searchQuery).map((key, index) => {
            if(searchQuery[key].length !== 0 && !key.includes('@')) {
                    return <QueryRowItem 
                        key={index} 
                        type={key} 
                        property={initialData[key]}
                        searchQuery={searchQuery}/>
            }
            return false;
        })
    );
}

export default QueryRow;