import React, {useState} from "react";
import {Alert, Button, Container, Form, Row, Spinner} from "react-bootstrap";
import {fetchUser} from "../redux/actions/authCreators";
import {connect} from "react-redux";

let LoginPage = ({
    error,
    fetchUser,
    fetching
}) => {
    const [login, setLogin] = useState('');
    const [pass, setPass] = useState('');
    return (
        <>
            {
                fetching
                ?
                    <Container className="d-flex justify-content-center align-items-center w-100 h-100">
                        <Spinner animation={"border"} />
                    </Container>
                :
                <Container className={"text-center h-100"} fluid>
                    <Row className={"h-100 text-center"}>
                        <Form className={"mx-auto my-auto"} style={{minWidth: '300px'}}>
                            <h1 className={"h3 mb-3"}>Sign in</h1>
                            <Form.Label htmlFor={"login"} />
                            <Form.Control
                                id={"login"}
                                placeholder={"Login"}
                                autoComplete={"username"}
                                value={login}
                                onChange={(event) => setLogin(event.target.value)}
                            />
                            <Form.Label htmlFor={"password"} />
                            <Form.Control
                                id={"password"}
                                placeholder={"Password"}
                                autoComplete={'current-password'}
                                type={"password"}
                                value={pass}
                                onChange={(event) => setPass(event.target.value)}
                            />
                            <hr/>
                            {
                                error && error.length
                                    ?
                                    <Alert variant={"danger"}>
                                        {error}
                                    </Alert>
                                    : null
                            }
                            <Button
                                block
                                onClick={() => {
                                    fetchUser(login, pass)
                                }}
                            >
                                Sign in
                            </Button>
                        </Form>
                    </Row>
                </Container>
            }
        </>
    );
}

const mapStateToProps = state => {
    return {
        error: state.authReducer.error,
        fetching: state.authReducer.fetching
    }
}

const mapDispatchToProps = dispatch => {
    return {
        fetchUser: (login, password) => dispatch(fetchUser(login, password))
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(LoginPage);