import {ADV} from "../actions/advCreator";

const initialState = {
    names: [{

    }],
    emails: [{

    }],
    phones: [{

    }],
    usernames: [{

    }],
    addresses: [{

    }],
    associates: [{

    }],
    educations: [{

    }],
    jobs: [{

    }],
    open: false
}

const advReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADV.SET_OPEN:
            return {
                ...state,
                open: true
            }
        case ADV.SET_CLOSE:
                return {
                    ...state,
                    open: false
                }
        default:
            return state;
    }
}

export default advReducer;
