import React from "react";
import {ListGroup} from "react-bootstrap";
import {withRouter} from "react-router-dom";
import hash from 'object-hash';
import DefProfile from '../../assets/img/defprofile.png';

const TopHistory = ({topHistory, getPersonById, history}) => {
    return (
        <ListGroup bsPrefix={"list-inline"} className={"text-center"} as={"ul"}>
            {
                topHistory.map(({name, imgUrl, searchHistoryId}) => (
                    <ListGroup.Item bsPrefix={"list-inline-item"} action={true} as={"li"} key={hash(searchHistoryId)}>
                        <div className="p-2">
                            <div onClick={() => {
                                getPersonById(searchHistoryId)
                                    .then(() => {
                                        history.push("/results");
                                    });
                            }}>
                                {
                                    imgUrl
                                    ? <img src={imgUrl} alt="Unable to load" className="img-fluid rounded-circle mb-3" style={{height: '85px', width: '85px'}}/>
                                    : <img src={DefProfile} alt="Unable to load" className="img-fluid mb-3" style={{height: '90px', width: '90px'}} />
                                }
                                <div className="text-center">
                                    <div className="text-sm">
                                        {name}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </ListGroup.Item>
                ))
            }
        </ListGroup>
    );
}

export default withRouter(TopHistory);