import * as API from '../../api/API';

export const AUTH = {
    LOGIN: 'LOGIN',
    LOGOUT: 'LOGOUT',
    LOGOUT_FAILED: 'LOGOUT_FAILED',
    FETCH_STARTED: 'FETCH_STARTED',
    FETCH_FAILED: 'FETCH_FAILED',
    FETCH_SUCCESSED: 'FETCH_SUCCESSED'
}

export const sendSignout = () => {
    return async (dispatch, getState) => {
        const { session_token } = getState().authReducer;
        API.signOut(session_token)
            .then(response => {
                dispatch(userLogout())
            })
            .catch(error => {
                // dispatch(userLogoutFailed())
                dispatch(userLogout())
                dispatch(fetchUserFailed('Not able to connect to the server'));
            });
    }
}

export const userLogout = () => {
    return {
        type: AUTH.LOGOUT
    }
}

export const userLogoutFailed = () => {
    return {
        type: AUTH.LOGOUT_FAILED
    }
}

export const fetchUser = (login, password) => {
    return async dispatch  => {
        dispatch(fetchUserStart());
        await API.signIn({
            login,
            password
        })
            .then(response => {
                if(response.status === 200) {
                    dispatch(userLogin(response.data));
                }
            })
            .catch(err => {
                if(err.response && err.response.status === 401) {
                    dispatch(fetchUserFailed('Wrong login or password'));
                } else if(err.response && err.response.status === 500) {
                    dispatch(fetchUserFailed('Server error'))
                } else {
                    dispatch(fetchUserFailed('Not able to connect to the server'));
                }
            });
    }
}

export const fetchUserStart = () => {
    return {
        type: AUTH.FETCH_STARTED
    }
}

export const fetchUserFailed = (error) => {
    return {
        type: AUTH.FETCH_FAILED,
        payload: {
            error
        }
    }
}

export const userLogin = (data) => {
    return {
        type: AUTH.LOGIN,
        payload: {
            data
        }
    }
}