import {search, searchOneLine} from "../../api/API";
import history from "../../history";
import { setClose } from "./advCreator";

export const PERSON = {
    SET_PERSON_INFO: 'SET_PERSON_INFO',
    SET_PERSONS: 'SET_PERSONS',
    CLEAR_PERSONS: 'CLEAR_PERSONS',
    SET_ERROR: 'SET_ERROR',
    CLEAR_ERROR: 'CLEAR_ERROR',
    IS_FETCHING: 'IS_FETCHING',
    SET_QUERY: 'SET_QUERY'
}

const responseHandler = (response, dispatch) => {
    if(response.status === 200) {
        if(response.data.person) {
            dispatch(
                setPersonInfo({
                    person: response.data.person,
                    searchQuery: response.data.query
                })
            );
        } else if(response.data.possible_persons) {
            dispatch(
                setPersons({
                    possible_persons: response.data.possible_persons,
                    searchQuery: response.data.query
                })
            );
        } else {
            dispatch(clearPersons());
        }
        dispatch(setQuery(response.data.query));
        dispatch(clearError());
        dispatch(setClose());
        return response.data['@persons_count'];
    }
}

const setQuery = searchQuery => {
    return {
        type: PERSON.SET_QUERY,
        searchQuery
    }
}

export const sendOneLine = (query, location) => {
    return async dispatch => {
        dispatch(isFetching(true));
        searchOneLine({
            query,
            location
        })
            .then((response) => {
                dispatch(isFetching(false));
                responseHandler(response, dispatch);
                history.push("/results");
            })
            .catch(error => {
                if(error.response) {
                    dispatch(setError(error.response.data.message));
                }
                dispatch(isFetching(false));
                dispatch(setClose());
            });
    }
}

export const clearError = () => {
    return {
        type: PERSON.CLEAR_ERROR
    }
}

export const sendAdvanced = data => {
    return async dispatch => {
        dispatch(isFetching(true));
        search(data)
            .then((response) => {
                dispatch(isFetching(false));
                responseHandler(response, dispatch);
                history.push('/results');
            })
            .catch(error => {
                if(error.response) {
                    dispatch(setError(error.response.data.message));
                }
                dispatch(isFetching(false));
                dispatch(setClose());
            });
    }
}

export const setPersonInfo = ({person, personsCount, searchQuery}) => {
    return {
        type: PERSON.SET_PERSON_INFO,
        payload: {
            person,
            personsCount,
            searchQuery
        }
    }
}

export const setPersons = ({possible_persons, personsCount, searchQuery}) => {
    return {
        type: PERSON.SET_PERSONS,
        payload: {
            possible_persons,
            personsCount,
            searchQuery
        }
    }
}

export const clearPersons = () => {
    return {
        type: PERSON.CLEAR_PERSONS
    }
}

export const setError = (status) => {
    return {
        type: PERSON.SET_ERROR,
        status
    }
}

export const isFetching = (isFetching) => {
    return {
        type: PERSON.IS_FETCHING,
        isFetching
    }
}