import React, {Component} from "react";
import {Col, Row} from "react-bootstrap";
import MainInfoRow from "./MainInfoRow";
import NamesRow from "./NamesRow";
import EmailsRow from "./EmailsRow";
import AddressesRow from "./AddressesRow";
import PhonesRow from "./PhonesRow";
import EducationsRow from "./EducationsRow";
import JobsRow from "./JobsRow";
import UrlsRow from "./UrlsRow";
import UsernamesRow from "./UsernamesRow";
import Results from '../Results/Results';
import RelationshipsRow from "./RelationshipsRow";

class Profile extends Component {

    componentDidMount() {
        const { setClose, open } = this.props;
        console.log(open);
        if(open) setClose();
    }

    render() {
        const {person, persons} = this.props;
        return (
            <>
                <Col md={9} ref={this.props.outerRef}>
                    <MainInfoRow person={person} />
                    <NamesRow names={person.names} />
                    <PhonesRow phones={person.phones} />
                    <EmailsRow emails={person.emails} />
                    <RelationshipsRow relationships={person.relationships} />
                    <AddressesRow addresses={person.addresses} />
                    <EducationsRow educations={person.educations} />
                    <JobsRow jobs={person.jobs} />
                    <UrlsRow urls={person.urls}/>
                    <UsernamesRow usernames={person.usernames} />
                    <Row className={"mx-2"}>
                        <Results persons={persons} />
                    </Row>
                </Col>
            </>
        );
    }
}


export default Profile;