import {SEARCH} from "../actions/historyCreators";

const initialState = {
    topHistory: [],
    getFromHistory: {

    },
    getHistory: {

    },
    users: [],
    error: null
}

const historyReducer = (state = initialState, action) => {
    switch (action.type) {
        case SEARCH.HISTORY_FAILED:
            return {
                ...state,
                error: action.payload.error
            }
        case SEARCH.SET_TOP_HISTORY:
            const { topHistory } = action.payload;
            return {
                ...state,
                topHistory
            }
        case SEARCH.SET_HISTORY:
            const { history } = action.payload;
            return {
                ...state,
                history
            }
        case SEARCH.SET_USERS:
            const { users } = action.payload;
            return {
                ...state,
                users
            }
        default:
            return state;
    }
}

export default historyReducer;