import React from "react";
import {Col, Row} from "react-bootstrap";
import {NavLink} from "react-router-dom";
import {sendAdvanced} from "../../redux/actions/personCreators";
import {connect} from "react-redux";

const ResultsRow = ({person, sendAdvanced}) => {
    return (
        <Row className="mb-5">
            <Col md={10}>
                <div className="h4 mb-2">
                    <NavLink
                        to={"/results"}
                        onClick={() => sendAdvanced(person)}>
                        {person.names[0].display}
                    </NavLink>
                </div>
                {
                    person.addresses.length
                    ?
                        (
                            <div className={"text-secondary font-weight-bold"}>
                                {person.addresses[0].display}
                            </div>
                        )
                    : null
                }
                {
                    person.educations.length
                    ?
                        (
                            <div className="text-secondary">
                                {person.educations[0].display}
                            </div>
                        )
                    : null
                }
            </Col>
            {
                person.images.length
                    ?
                        (
                            <Col md={2}>
                                <img alt='' src={person.images[0].url} style={{height: '65px', width: '65px'}}className={"rounded-circle mb-3"}/>
                            </Col>
                        )
                    : null
            }
            
        </Row>
    );
}

const mapDispatchToProps = dispatch => {
    return {
        sendAdvanced: (data) => dispatch(sendAdvanced(data))
    }
}

export default connect(
    null,
    mapDispatchToProps
)(ResultsRow);