import React from "react";
import {Col, OverlayTrigger, Row, Nav} from "react-bootstrap";
import {NavLink} from "react-router-dom";
import hash from 'object-hash';
import PopoverOverlay from "./Popover";

const UrlsRow = ({urls}) => {
    return (
        urls && urls.length
            ?
            (
                <Row className={"mx-2"}>
                    <Col md={12} className={"mb-5"}>
                        <h2 className="h4 py-2 border-bottom mb-3">
                            URLs
                        </h2>
                        <Row>
                            <Col md={12}>
                                {
                                    urls.map(url => (
                                        <div className="mb-2" key={hash(url)}>
                                            <OverlayTrigger trigger={['hover', 'focus']} placement="right"  overlay={(data) => {
                                                return PopoverOverlay({...data, data:url, pointer:"url"});
                                            }}>
                                                <a href={url.url} target="_blank" rel="noopener noreferrer">
                                                    {url.url}
                                                </a>
                                            </OverlayTrigger>

                                            <span className="ml-3 text-muted">
                                                { url['@name'] }
                                            </span>
                                        </div>
                                    ))
                                }
                            </Col>
                        </Row>
                    </Col>
                </Row>
            )
            : null
    );
}

export default UrlsRow;