import React, {Component} from "react";
import {Container, Dropdown, Form, Row, Col} from "react-bootstrap";
import {KeyboardDatePicker, MuiPickersUtilsProvider} from '@material-ui/pickers';
import DateFnsUtils from "@date-io/date-fns";
import HistoryRecords from "../components/HistoryRecords/HistoryRecords";
import {getHistory} from "../redux/actions/historyCreators";
import {connect} from "react-redux";
import * as moment from "moment";
import hash from 'object-hash';
import ReactPaginate from 'react-paginate';
import Preloader from '../components/Preloader/index';
import { setClose } from "../redux/actions/advCreator";

class HistoryPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            pageSize: 5,
            currentPage: 1,
            userLogin: props.login,
            startDate: null,
            endDate: null
        }

        this.startOnChange = this.startOnChange.bind(this);
        this.endOnChange = this.endOnChange.bind(this);
        this.handlePageChange = this.handlePageChange.bind(this);
        this.userChange = this.userChange.bind(this);
    }

    handlePageChange(currentPage) {
        this.setState({
            currentPage
        }, () => {
            this.historyQuery();
        });
    }

    componentDidMount() {
        if(this.props.open) this.props.setClose();
        this.historyQuery();
    }

    historyQuery() {
        const {
            pageSize,
            currentPage,
            userLogin,
            startDate,
            endDate
        } = this.state;
        const data = {
            pageSize,
            currentPage: currentPage - 1,
            userLogin,
        };
        if(startDate){
            data.startDate = moment(startDate).format('yyyy-MM-DD');
        }
        if(endDate) {
            data.endDate = moment(endDate).format('yyyy-MM-DD');
        }
        const { getHistory } = this.props;
        getHistory(data);
    }

    startOnChange(date) {
        this.setState({
            startDate: date
        }, () => {
            this.historyQuery();
        });
    }

    endOnChange(date) {
        this.setState({
            endDate: date
        }, () => {
            this.historyQuery();
        });
    }

    userChange(event) {
        this.setState({
            userLogin: event.target.value,
            currentPage: 1
        }, () => {
            this.historyQuery();
        })
    }

    render() {
        const {
            startDate,
            endDate,
            userLogin
        } = this.state;
        const {
            history,
            users,
            isFetching
        } = this.props;
        return (
            <main className="py-4">
                <Container>
                    <div className="border-bottom px-3 py-2 mb-5">
                        <div className="d-flex">
                            <div className="h2 text-primary">
                                History
                            </div>
                        </div>
                    </div>
                    {
                        !isFetching
                            ?
                                (
                                    <>
                                        <Row className="px-3 mb-5">
                                            <Col xs={6}>
                                                <Col xs={6}>
                                                    <Dropdown className={"bootstrap-select"}>
                                                        <Form.Control as={"select"} value={userLogin} onChange={this.userChange}>
                                                            {
                                                                users.map(user => <option key={hash(user)} value={user.login}>{user.login}</option>)
                                                            }
                                                        </Form.Control>
                                                    </Dropdown>
                                                </Col>
                                            </Col>
                                            <Col xs={6}>
                                                <Dropdown className={"pb-5 text-right"}>
                                                    <div className="form-inline">
                                                        <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                                            <KeyboardDatePicker
                                                                label={"Start Date"}
                                                                value={startDate}
                                                                onChange={this.startOnChange}
                                                            />
                                                            &nbsp;
                                                            &nbsp;
                                                            <KeyboardDatePicker
                                                                label={"End Date"}
                                                                value={endDate}
                                                                onChange={this.endOnChange}
                                                            />
                                                        </MuiPickersUtilsProvider>
                                                    </div>
                                                </Dropdown>
                                            </Col>
                                        </Row>
                                        <div className="px-3 mb-5" id="historyTable">
                                            <HistoryRecords historyRecords={history}/>
                                        </div>
                                    </>
                                )
                            : <Preloader />
                    }
                    {
                        history && history.records && history.records.length
                        ?
                            (
                                <ReactPaginate
                                    breakClassName={"page-item"}
                                    breakLinkClassName={"page-link"}
                                    previousLinkClassName={"page-link"}
                                    previousClassName={"page-item"}
                                    nextClassName={"page-item"}
                                    nextLinkClassName={"page-link"}
                                    pageClassName={"page-item"}
                                    pageLinkClassName={"page-link"}
                                    activeClassName={"active"}
                                    containerClassName={"pagination"}
                                    pageCount={history.pageCount}
                                    pageRangeDisplayed={5}
                                    marginPagesDisplayed={1}
                                    onPageChange={(test) => this.handlePageChange(test.selected)}
                                />
                            )
                        : null
                    }

                </Container>
            </main>
        );
    }
}

const mapStateToProps = state => {
    return {
        history: state.historyReducer.history,
        users: state.historyReducer.users,
        login: state.authReducer.data.login,
        isFetching: state.personReducer.isFetching,
        open: state.advReducer.open
    }
}

const mapDispatchToProps = dispatch => {
    return {
        getHistory: (data) => dispatch(getHistory(data)),
        setClose: () => dispatch(setClose())
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(HistoryPage);