import React from "react";
import {Col, OverlayTrigger, Row} from "react-bootstrap";
import {NavLink} from "react-router-dom";
import hash from 'object-hash';
import PopoverOverlay from "./Popover";

const JobsRow = ({jobs}) => {
    return (
        jobs.length
            ?
            (
                <Row className={"mx-2"}>
                    <Col md={12} className={"mb-5"}>
                        <h2 className="h4 py-2 border-bottom mb-3">
                            Jobs
                        </h2>
                        <Row>
                            <Col md={12}>
                                {
                                    jobs.map(job => (
                                        <div className="mb-2" key={hash(job)}>
                                            <OverlayTrigger trigger={['hover', 'focus']} placement="right"  overlay={(data) => {
                                                return PopoverOverlay({...data, data:job, pointer:"display"});
                                            }}>
                                                <span className={"text-muted"}>
                                                    {job.display}
                                                </span>
                                            </OverlayTrigger>

                                            <span className="ml-3 text-muted">
                                                { job.industry }
                                            </span>
                                        </div>
                                    ))
                                }
                            </Col>
                        </Row>
                    </Col>
                </Row>
            )
            : null
    );
}

export default JobsRow;