import React from "react";
import {Container, Navbar, Col} from "react-bootstrap";
import {NavLink, withRouter} from "react-router-dom";
import {connect} from "react-redux";
import ProfileMenu from "./ProfileMenu";
import SearchComponent from "../../containers/SearchContainer";
import AdvancedSearchContainer from "../../containers/AdvancedSearchContainer";

let Header = ({
    location: {
        pathname
    },
    loggedIn
}) => {
    return (
        <header className={"w-100 mb-auto"}>
            <Navbar expand={"md"} bg={"light"} className={`pt-4 ${pathname !== "/" ? "shadow-sm" : null}`}>
                <Container>
                    { pathname !== '/'
                        ? (
                            <>
                                <Navbar.Brand as={NavLink} to={"/"} className={"mr-5"}>
                                    <h1 className="display-3 text-primary">
                                        MGAP
                                    </h1>
                                </Navbar.Brand>
                            </>
                        )
                        : null
                    }
                    {
                        loggedIn
                            ? <ProfileMenu />
                            :
                            <NavLink to={"/login"}>
                                Sign in
                            </NavLink>
                    }
                    { pathname !== '/'
                        ? 
                            (
                                <Col md={8}>
                                    <SearchComponent />
                                </Col>
                            )
                        : null
                    }
                </Container>
            </Navbar>
            <Navbar>
                <Container>
                    { pathname !== '/'
                        ? <AdvancedSearchContainer />
                        : null
                    }
                </Container>
            </Navbar>
        </header>
    );
}

const mapStateToProps = (state, ownProps) => {
    return {
        ...ownProps,
        loggedIn: state.authReducer.loggedIn,
    }
}

Header = withRouter(Header);
Header = connect(
    mapStateToProps,
    null
)(Header);

export default Header;
