import React, {Component} from "react";
import {NavLink} from "react-router-dom";
import * as Icon from "react-feather";
import {Col} from "react-bootstrap";
import QueryRow from "./QueryRow";

class ResultsSidebar extends Component {
	render() {
		const {searchQuery} = this.props;
		return (
			<Col md={3} id="resultsSidebar" className="d-xl-block d-lg-block">
				<h6 className={"pb-2"}>Results for</h6>
				{searchQuery && <QueryRow searchQuery={searchQuery} />
				}
				<hr className={"mt-3 mb-4"}/>
				<NavLink to={"#"} onClick={this.props.handleClick} className={"text-decoration-none"}>
					<Icon.Printer className={"f-sm"}/>
					{" Print"}
				</NavLink>
			</Col>
		);
	}
}

export default ResultsSidebar;