import AdvancedSearch from "../components/AdvancedSearch/AdvancedSearch";
import {connect} from "react-redux";
import {Col, Collapse, Row} from "react-bootstrap";
import React from "react";
import {sendAdvanced} from "../redux/actions/personCreators";
import Preloader from "../components/Preloader/Preloader";

const AdvancedSearchContainer = props => {
    return (
        <Row>
            <Col md={12}>
                <Collapse in={props.open}>
                    <div id="collapseAdvanced">
                        {props.isFetching &&<Preloader/>}
                        <AdvancedSearch {...props} />
                    </div>
                </Collapse>
            </Col>
        </Row>
    );
}

const mapStateToProps = (state, ownProps) => {
    return {
        initialValues: state.advReducer,
        isFetching: state.personReducer.isFetching,
        open: state.advReducer.open,
        ...ownProps
    };
}

const mapDispatchToProps = dispatch => {
    return {
        send: (values) => {
            let data = {}
            Object.keys(values).forEach(key => {
                const array = Array.prototype.slice.call(values[key]);
                data[key] = array.filter(element => Object.keys(element).length)
            });
            delete data.open;
            return dispatch(sendAdvanced(data));
        }
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(AdvancedSearchContainer);