import React from "react";
import {Col, OverlayTrigger, Row} from "react-bootstrap";
import {NavLink} from "react-router-dom";
import hash from 'object-hash';
import PopoverOverlay from "./Popover";

const EducationsRow = ({educations}) => {
    return (
        educations.length
            ?
            (
                <Row className={"mx-2"}>
                    <Col md={12} className={"mb-5"}>
                        <h2 className="h4 py-2 border-bottom mb-3">
                            Educations
                        </h2>
                        <Row>
                            <Col md={12}>
                                {
                                    educations.map(education => (
                                        <div className="mb-2" key={hash(education)}>
                                            <OverlayTrigger trigger={['hover', 'focus']} placement="right" data={education} overlay={(data) => {
                                                return PopoverOverlay({...data, data:education, pointer:"display"});
                                            }}>
                                                <span className={"text-muted"}>
                                                    {education.display}
                                                </span>
                                            </OverlayTrigger>
                                        </div>
                                    ))
                                }
                            </Col>
                        </Row>
                    </Col>
                </Row>
            )
            : null
    );
}

export default EducationsRow;