import React from "react";
import {Col, NavLink, Form as FormBoot, Row, Button} from "react-bootstrap";
import * as Icon from "react-feather";
import {clearSubmitErrors, Field, FieldArray, Form, reduxForm} from "redux-form";
import hash from 'object-hash';
import validate from "./validate";

const AddRemoveLink = ({
    text,
    index,
    fields
}) => (
    <NavLink
        className={`text-sm ${index ? "text-danger" : null}`}
        onClick={() => index ? fields.remove(index) : (fields.length < 5 ? fields.push({}) : null)}
    >
        {
            !index
            ?
            (<>
                <Icon.Plus/>
                {` Add another ${text}`}
            </>)
            : "Remove"
        }
    </NavLink>
)

const renderField = ({
    placeholder,
    type,
    meta: {
        touched,
        error
    },
    input
}) => (
    <>
        <FormBoot.Control
            placeholder={placeholder}
            type={type}
            {...input}
            className={touched && error && "error-input"}
        />
    </>
);

const renderNames = ({fields}) => (
    <>
        {
            fields.map((name, index) => (
                <Row key={hash(index)}>
                    <Col md={2} as={"label"} className={"font-weight-bold"}>
                        {!index && fields.name[0].toUpperCase() + fields.name.slice(1)}
                    </Col>
                    <Col md={7}>
                        <div className="form-row">
                            <Col md={4} as={FormBoot.Group}>
                                <Field
                                    placeholder={"First Name"}
                                    name={`${name}.first`}
                                    type={"text"}
                                    component={renderField}
                                />
                            </Col>
                            <Col md={4} as={FormBoot.Group}>
                                <Field
                                    placeholder={"Middle Name"}
                                    name={`${name}.middle`}
                                    type={"text"}
                                    component={renderField}
                                />
                            </Col>
                            <Col md={4} as={FormBoot.Group}>
                                <Field
                                    placeholder={"Last Name"}
                                    name={`${name}.last`}
                                    type={"text"}
                                    component={renderField}
                                />
                            </Col>
                        </div>
                    </Col>
                    <Col md={3}>
                        <AddRemoveLink fields={fields} text={'Name'} index={index}/>
                    </Col>
                </Row>
            ))
        }
    </>
);

