let AdvancedSearch = ({handleSubmit, error, reset, dirty}) => {
    return (
        <>
            <Form onSubmit={handleSubmit}>
                <div id="advNames" className="mb-3">
                    <FieldArray
                        name={"names"}
                        component={renderNames}
                    />
                </div>
                <div id="advEmails" className="mb-3">
                    <FieldArray
                        name={"emails"}
                        component={renderEmails}
                    />
                </div>
                <div id="advPhones" className="mb-3">
                    <FieldArray
                        name={"phones"}
                        component={renderPhones}
                    />
                </div>
                <div id="advUsernames" className="mb-3">
                    <FieldArray
                        name={"usernames"}
                        component={renderUsernames}
                    />
                </div>
                <div id="advAddresses" className="mb-3">
                    <FieldArray
                        name={"addresses"}
                        component={renderAddresses}
                    />
                </div>
                <div id="advAssociates" className="mb-3">
                    <FieldArray
                        name={"associates"}
                        component={renderAssociates}
                    />
                </div>
                <div id="advEducations" className="mb-3">
                    <FieldArray
                        name={"educations"}
                        component={renderEducations}
                    />
                </div>
                <div id="advJobs" className="mb-3">
                    <FieldArray
                        name={"jobs"}
                        component={renderJobs}
                    />
                </div>
                {error && (
                    <div className={"mb-3"}>
                        <p className={"error-text"}>{error}</p>
                    </div>
                )}
                <Row>
                    <Col md={6}>
                        <Button
                            id={"searchButton"}
                            size={"lg"}
                            type={"submit"}
                            className={"mr-1"}
                            disabled={!dirty}
                        >
                            Search
                        </Button>
                        <Button
                            size={'lg'}
                            className={"btn btn-danger ml-1"}
                            onClick={reset}
                        >
                            Clear Form
                        </Button>
                    </Col>
                </Row>
            </Form>
        </>
    );
}

export default reduxForm({
    form: 'advancedSearch',
    destroyOnUnmount: false,
    onSubmit: (values, dispatch, props) => {
        validate(values);
        props.send(values);
        dispatch(clearSubmitErrors('advancedSearch'));
    }
})(AdvancedSearch);
