import * as React from 'react';
import {capitalize} from "../../helpers/capitalize";

export const QueryRowItem = ({type, property, searchQuery}) => {
	return (
		<div className={"text-muted"}>
			{
				searchQuery[type].length
				?
					(
						searchQuery[type].map(el => property.map(item => {
							return (
								<div>
									<span className="text-primary " style={{fontWeight: '500'}}>{capitalize(type)}: </span>
									{el[item]}
								</div>
								)
							}
						))
					)
				: null
			}
		</div>
	);
};