import React from "react";
import {Col, OverlayTrigger, Row} from "react-bootstrap";
import {NavLink} from "react-router-dom";
import {typeFixer} from "../../helpers/typeFixer";
import hash from 'object-hash';
import PopoverOverlay from "./Popover";

const EmailsRow = ({emails}) => {
    return (
        emails.length
            ?
            (
                <Row className={"mx-2"}>
                    <Col md={12} className={"mb-5"}>
                        <h2 className="h4 py-2 border-bottom mb-3">
                            Emails
                        </h2>
                        <Row>
                            <Col md={12}>
                                {
                                    emails.map(email => (
                                        <div className="mb-2" key={hash(email)}>
                                            <OverlayTrigger trigger={['hover', 'focus']} placement="right" data={email} overlay={(data) => {
                                                return PopoverOverlay({...data, data:email, pointer:"address"});
                                            }}>
                                                <span className={"text-muted"}>
                                                    {email.address}
                                                </span>
                                            </OverlayTrigger>
                                            <span className="ml-3 text-muted">
                                                { typeFixer(email['@type']) }
                                            </span>
                                        </div>
                                    ))
                                }
                            </Col>
                        </Row>
                    </Col>
                </Row>
            )
            : null
    );
}

export default EmailsRow;