import React from "react";
import {Button, FormControl,} from "react-bootstrap";
import * as Icon from "react-feather";
import {Field, reduxForm, Form} from "redux-form";
import {connect} from "react-redux";
import {sendOneLine} from "../../redux/actions/personCreators";

const RenderField = ({input, placeholder, className, size}) => {
    return (
        <FormControl
            {...input}
            placeholder={placeholder}
            className={className}
            size={size}
        />
    )
}

let LineSearch = ({
    handleSubmit,
    open
}) => {
    return (
        <Form className={"flex-fill"} onSubmit={handleSubmit}>
            <div className={"input-group"}>
                <Field
                    component={RenderField}
                    name={"query"}
                    placeholder={"Name, Email, Username or Phone"}
                    size={"lg"}
                />
                <Field
                    component={RenderField}
                    name={"location"}
                    placeholder={"Location (optionally)"}
                    className={"d-none d-xl-block"}
                    size={"lg"}
                />
                <div className="input-group-append">
                    <Button
                        type={"submit"}
                        disabled={open}
                    >
                        <Icon.Search />
                    </Button>
                </div>
            </div>
        </Form>
    );
}

LineSearch = reduxForm({
    form: 'lineSearch',
    destroyOnUnmount: false,
    onSubmit: (values, dispatch, props) => {
        const {query, location} = values;
        dispatch(sendOneLine(query, location));
    }
})(LineSearch);

const mapStateToProps = (state, ownProps) => {
    return {
        open: state.advReducer.open,
        initialState: state.oneLineReducer,
        ...ownProps
    }
}

export default connect(
    mapStateToProps,
    null
)(LineSearch)