import React, {Component} from "react";
import {Col} from "react-bootstrap";
import ResultsRow from "./ResultsRow";
import hash from 'object-hash';
import { connect } from "react-redux";
import { setClose } from "../../redux/actions/advCreator";

class Results extends Component {

    componentDidMount() {
        const { setClose, open } = this.props;
        console.log(open);
        if(open) setClose();
    }

    render() {
        const { persons } = this.props;
        return (
            <>
                <Col id="resultsMain" md={12} ref={this.props.outerRef}>
                    <h2 className="h4 py-2 border-bottom mb-3">
                        List of possible persons
                    </h2>
                    {
                        persons && persons.length 
                        ?
                            persons.map(person => (
                                <ResultsRow person={person} key={hash(person)}/>
                            ))
                        : <span className="mx-auto text-muted pb-3">None found</span>
                    }
                </Col>
            </>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        ...ownProps,
        open: state.advReducer.open
    }
}

const mapDispatchToProps = dispatch => {
    return {
        setClose: () => dispatch(setClose())
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Results);