import LineSearch from "../LineSearch/LineSearch";
import {NavLink, Row, Col} from "react-bootstrap";
import React, { Component } from "react";
import { connect } from 'react-redux';
import {withRouter} from "react-router-dom";
import { setOpen, setClose } from "../../redux/actions/advCreator";

class SearchComponent extends Component {
    render() {
        const { 
            onFieldChange,
            setOpen,
            setClose,
            open,
            errorHandle
        } = this.props;
        return (
            <>
            <div className="w-100 d-flex py-3 order-md-1">
                <LineSearch onFieldChange={onFieldChange}/>
                <div className="mx-3 align-self-center d-none d-xl-block d-lg-block">
                    <NavLink
                        id={"advancedButton"}
                        className={"font-weight-bold text-decoration-none"}
                        role={"button"}
                        onClick={() => open ? setClose() : setOpen()}
                    >
                        {open ? "Back to basic" : "Advanced"}
                    </NavLink>
                </div>
            </div>
            <Row>
                <Col md={5}>
                    {
                        errorHandle && 
                            (
                                <div className="w-100 d-flex py-3 order-md-1">
                                    <span className="error-text">{errorHandle}</span>
                                </div>
                            )
                    }
                </Col>
            </Row>
        </>
        );
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        ...ownProps,
        open: state.advReducer.open,
        errorHandle: state.personReducer.errorHandle
    }
}

const mapDispatchToProps = dispatch => {
    return {
        setOpen: () => dispatch(setOpen()),
        setClose: () => dispatch(setClose())
    }
}


export default connect(
    mapStateToProps,
    mapDispatchToProps
)(withRouter(SearchComponent));