import React from "react";
import {Col, OverlayTrigger, Row} from "react-bootstrap";
import {NavLink} from "react-router-dom";
import {typeFixer} from "../../helpers/typeFixer";
import hash from 'object-hash';
import PopoverOverlay from "./Popover";

const AddressesRow = ({addresses}) => {
    return (
        addresses.length
            ?
            (
                <Row className={"mx-2"}>
                    <Col md={12} className={"mb-5"}>
                        <h2 className="h4 py-2 border-bottom mb-3">
                            Addresses
                        </h2>
                        <Row>
                            <Col md={12}>
                                {
                                    addresses.map(address => (
                                        <div className="mb-2" key={hash(address)}>
                                            <OverlayTrigger trigger={['hover', 'focus']} placement="right" data={address} overlay={(data) => {
                                                return PopoverOverlay({...data, data:address, pointer:"display"});
                                            }}>
                                                <span className={"text-muted"}>
                                                    {address.display}
                                                </span>
                                            </OverlayTrigger>
                                            <span className="ml-3 text-muted display-6">
                                                { typeFixer(address['@type']) }
                                            </span>
                                        </div>
                                    ))
                                }
                            </Col>
                        </Row>
                    </Col>
                </Row>
            )
            : null
    );
}

export default AddressesRow;