import React from "react";
import {Col, NavLink, OverlayTrigger, Row} from "react-bootstrap";
import {typeFixer} from "../../helpers/typeFixer";
import hash from 'object-hash';
import PopoverOverlay from "./Popover";
import {connect} from "react-redux";
import {sendOneLine} from "../../redux/actions/personCreators";

const RelationshipsRow = ({relationships, sendOneLine}) => {
    return (
        relationships && relationships.length
            ?
            (
                <Row className={"mx-2"}>
                    <Col md={12} className={"mb-5"}>
                        <h2 className="h4 py-2 border-bottom mb-3">
                            Associated with
                        </h2>
                        <Row>
                            <Col md={12}>
                                {
                                    relationships.map(relationship => (
                                        <div className="mb-2" key={hash(relationship)}>
                                            <OverlayTrigger trigger={['hover', 'focus']} placement="right" data={relationship} overlay={(data) => {
                                                return PopoverOverlay({...data, data:relationship, pointer:"display"});
                                            }}>
                                                {
                                                    relationship.names && relationship.names.length
                                                        ?
                                                            (
                                                                <a
                                                                    href={"#"}
                                                                    onClick={() => {
                                                                        sendOneLine(relationship.names[0].display);
                                                                    }}>
                                                                    {relationship.names[0].display}
                                                                </a>
                                                            )
                                                        : null
                                                }
                                            </OverlayTrigger>
                                            <span className="ml-3 text-muted display-6">
                                                { typeFixer(relationship['@subtype']) }
                                            </span>
                                        </div>
                                    ))
                                }
                            </Col>
                        </Row>
                    </Col>
                </Row>
            )
            : null
    );
}

const mapDispatchToProps = dispatch => {
    return {
        sendOneLine: (query) => dispatch(sendOneLine(query))
    }
}

export default connect(
    null,
    mapDispatchToProps
)(RelationshipsRow);