import React, {useRef, useState} from "react";
import {Col, OverlayTrigger, Row} from "react-bootstrap";
import {NavLink} from "react-router-dom";
import hash from 'object-hash';
import PopoverOverlay from "./Popover";

const NamesRow = ({names}) => {
    const [show, setShow] = useState(false);
    const ref = useRef();
    return (
        names.length
        ?
            (
                <Row className={"mx-2"}>
                    <Col md={12} className={"mb-5"}>
                        <h2 className="h4 py-2 border-bottom mb-3">
                            Names
                        </h2>
                        <Row>
                            <Col md={12}>
                                {
                                    names.map(name => (
                                        <div className="mb-2" key={hash(name)}>
                                            <OverlayTrigger trigger={['hover', 'focus']} placement="right"  overlay={(data) => {
                                                return PopoverOverlay({...data, data:name, pointer:"display"});
                                            }}>
                                                <span className={"text-muted"}>
                                                    {name.display}
                                                </span>
                                            </OverlayTrigger>
                                        </div>
                                    ))
                                }
                            </Col>
                        </Row>
                    </Col>
                </Row>
            )
        : null
    );
}

export default NamesRow;