import React from "react";
import {Col, NavLink, Row} from "react-bootstrap";
import {withRouter} from "react-router-dom";
import * as Icon from "react-feather";
import {getPersonById} from "../../redux/actions/historyCreators";
import {connect} from "react-redux";
import hash from 'object-hash';
import QueryRow from "../ResultsSidebar/QueryRow";

let HistoryRecords = ({historyRecords, history, getPersonById}) => {
    return (
        historyRecords && historyRecords.records.length
        ?
            (
                historyRecords.records.map((record, index) => (
                    <Row className={"pt-2 pb-5 border-bottom"}  key={hash(index)}>
                        <Col md={3} className={"text-nowrap"}>
                            {record.searchDate}
                        </Col>
                        <Col md={4} className={"text-truncate"}>
                            {record.name}
                        </Col>
                        <Col md={3} className={"text-truncate"}>
                            <QueryRow searchQuery={record.query} />
                        </Col>
                        <Col md={2} className={"text-right text-nowrap"}>
                            <NavLink
                                onClick={() => {
                                    getPersonById(record.searchHistoryId)
                                        .then(() => {
                                            history.push('/results')
                                        })
                                }}
                            >
                                <Icon.Copy className={"mx-2"}/>
                            </NavLink>
                        </Col>
                    </Row>
                ))
            )
        : <h1 className={"h3"}>No results found for the dates</h1>
    );
}

HistoryRecords = withRouter(HistoryRecords);

const mapDispatchToProps = dispatch => {
    return {
        getPersonById: (id) => dispatch(getPersonById(id))
    }
}

export default connect(
    null,
    mapDispatchToProps
)(HistoryRecords);