import React from "react";
import {Col, OverlayTrigger, Row} from "react-bootstrap";
import {NavLink} from "react-router-dom";
import hash from 'object-hash';
import PopoverOverlay from "./Popover";

const UsernamesRow = ({usernames}) => {
    return (
        usernames && usernames.length
            ?
            (
                <Row className={"mx-2"}>
                    <Col md={12} className={"mb-5"}>
                        <h2 className="h4 py-2 border-bottom mb-3">
                            Usernames
                        </h2>
                        <Row>
                            <Col md={12}>
                                {
                                    usernames.map(username => (
                                        <div className="mb-2" key={hash(username)}>
                                            <OverlayTrigger trigger={['hover', 'focus']} placement="right"  overlay={(data) => {
                                                return PopoverOverlay({...data, data:username, pointer:"content"});
                                            }}>
                                                <span className={"text-muted"}>
                                                    {username.content}
                                                </span>
                                            </OverlayTrigger>
                                        </div>
                                    ))
                                }
                            </Col>
                        </Row>
                    </Col>
                </Row>
            )
            : null
    );
}

export default UsernamesRow;