import {topHistory, getHistory as history, getFromHistory, getUsers} from '../../api/API';
import {clearError, setError, setPersonInfo, setPersons, isFetching, clearPersons} from "./personCreators";

export const SEARCH = {
    SET_TOP_HISTORY: 'SET_TOP_HISTORY',
    SET_HISTORY: 'SET_HISTORY',
    SET_USERS: 'SET_USERS',
    HISTORY_FAILED: 'HISTORY_FAILED'
}

export const getPersonById = (id) => {
    return async dispatch => {
        dispatch(isFetching(true));
        await getFromHistory(id)
            .then(response => {
                dispatch(isFetching(false));
                dispatch(clearPersons());
                if(response.status === 200) {
                    if(response.data.person) {
                        dispatch(setPersonInfo({
                            person: response.data.person,
                            personsCount: response.data['@persons_count'],
                            searchQuery: response.data.query
                        }));
                    } else if(response.data.possible_persons) {
                        dispatch(setPersons({
                            possible_persons: response.data.possible_persons,
                            personsCount: response.data['@persons_count'],
                            searchQuery: response.data.query
                        }));
                    }
                }
            })
            .catch(error => {
                dispatch(isFetching(false));
                dispatch(clearPersons());
                if(error.response && error.response.status === 400) {
                    dispatch(setError('Bad request'));
                } else if (error.response && error.response.status === 401) {
                    dispatch(setError('You\'re not authorized'));
                } else if (error.response && error.response.status === 500) {
                    dispatch(setError('Server problem appeared'))
                } else {
                    dispatch(setError('Not able to connect to the server'));
                }
            });
    }
}

export const getTopHistory = () => {
    return async dispatch => {
        dispatch(isFetching(true));
        await topHistory()
            .then(response => {
                dispatch(isFetching(false));
                dispatch(clearPersons());
                if(response.status === 200) {
                    dispatch(setTopHistory(response.data))
                    dispatch(clearError())
                }
            })
            .catch(error => {
                dispatch(clearPersons());
                dispatch(isFetching(false));
                if(error.response && error.response.status === 400) {
                    dispatch(topHistoryFailed('Bad request'));
                } else if (error.response && error.response.status === 401) {
                    dispatch(topHistoryFailed('You\'re not authorized'));
                } else {
                    dispatch(setError('Not able to connect to the server'));
                }
            });
    }
}

export const setTopHistory = (topHistory) => {
    return {
        type: SEARCH.SET_TOP_HISTORY,
        payload: {
            topHistory
        }
    }
}

export const getHistory = (data) => {
    return async dispatch => {
        dispatch(isFetching(true));
        history(data)
            .then(response => {
                dispatch(clearPersons());
                dispatch(isFetching(false));
                if(response.status === 200) {
                    dispatch(setHistory(response.data));
                    dispatch(clearError())
                }
            })
            .catch(error => {
                dispatch(isFetching(false));
                dispatch(clearPersons());
                if(error.response && error.response.status === 400) {
                    dispatch(topHistoryFailed('Bad request'));
                } else if (error.response && error.response.status === 401) {
                    dispatch(topHistoryFailed('You\'re not authorized'));
                }else {
                    dispatch(setError('Not able to connect to the server'));
                }
            });
        getUsers()
            .then(response => {
                if(response.status === 200) {
                    dispatch(setUsers(response.data));
                    dispatch(clearError())
                }
            })
            .catch(error => {
                if(error.response && error.response.status === 400) {
                    dispatch(topHistoryFailed('Bad request'));
                } else if (error.response && error.response.status === 401) {
                    dispatch(topHistoryFailed('You\'re not authorized'));
                } else {
                    dispatch(setError('Not able to connect to the server'));
                }
            })
    }
}

const topHistoryFailed = error => {
    return {
        type: SEARCH.HISTORY_FAILED,
        payload: {
            error
        }
    }
}

export const setHistory = (history) => {
    return {
        type: SEARCH.SET_HISTORY,
        payload: {
            history
        }
    }
}

export const setUsers = users => {
    return {
        type: SEARCH.SET_USERS,
        payload: {
            users
        }
    }
}