import {SubmissionError} from "redux-form";

export default values => {
    const errors = {
        phones: []
    }
    let flagError = false, flagEmpty = true;
    Object.keys(values).forEach(key => {
        const array = Array.prototype.slice.call(values[key]);
        array.forEach(element => {
            if(Object.keys(element).length) {
                flagEmpty = false
            }
        });
    });
    if(values.phones) {
        values.phones.forEach((phone, index) => {
            if(phone.code && !phone.number) {
                errors.phones[index] = {number:'Required'}
                flagError = true;
            }
        });
    }
    if(flagError) throw new SubmissionError({...errors, _error: 'Fill in recommended values'});
    else if(flagEmpty) throw new SubmissionError({
        _error: `Sorry, search is not available with the given information. 
        Please make sure to fill in, at least, one of the following fields: 
        Name, Email, a valid Phone number and Country code, Username or a full US address`
    });
}