import React, {Component} from "react";
import {Col, Row} from "react-bootstrap";
import {NavLink as Link} from 'react-router-dom';
import {getPersonById, getTopHistory} from "../redux/actions/historyCreators";
import {connect} from "react-redux";
import TopHistory from "../components/TopHistory/TopHistory";
import SearchComponent from "../containers/SearchContainer";
import AdvancedSearchContainer from "../containers/AdvancedSearchContainer";
import {setClose} from "../redux/actions/advCreator";
import Preloader from "../components/Preloader/Preloader";

class StartPage extends Component {

    componentDidMount() {
        const { getTopHistory, setClose, open } = this.props;
        if(open) setClose();
        getTopHistory();
    }


    render() {
        const { loggedIn, topHistory, getPersonById, isFetching } = this.props;
        return(
            <main className={"px-2 mb-5"}>
                {
                    !isFetching
                    ?
                        (
                            <>
                                <h1 className={"display-3 text-primary mb3"}>MGAP</h1>
                                <SearchComponent />
                                <AdvancedSearchContainer />
                                <Row id={"relatedSearch"} className={"mb-4 pt-5"}>
                                    <Col md={12}>
                                        <TopHistory topHistory={topHistory} getPersonById={getPersonById}/>
                                    </Col>
                                </Row>
                                {
                                    loggedIn
                                    ?
                                        <Row>
                                            <Col className={"text-center"}>
                                                <Link to={"/history"} >
                                                    View full history
                                                </Link>
                                            </Col>
                                        </Row>
                                    : null
                                }
                            </>
                        )
                    : 
                        (
                            <Row>
                                <Col md={12}>
                                    <Preloader />
                                </Col>
                            </Row>
                        )
                }
            </main>
        );
    }
}

const mapStateToProps = state => {
    return {
        loggedIn: state.authReducer.loggedIn,
        isFetching: state.personReducer.isFetching,
        topHistory: state.historyReducer.topHistory,
        person: state.personReducer.person,
        possible_persons: state.personReducer.possible_persons,
        error: state.personReducer.errorHandle,
        open: state.advReducer.open
    }
}

const mapDispatchToProps = dispatch => {
    return {
        getTopHistory: () => dispatch(getTopHistory()),
        getPersonById: (searchHistoryId) => dispatch(getPersonById(searchHistoryId)),
        setClose: () => dispatch(setClose())
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(StartPage);