import React from "react";
import {Col, OverlayTrigger, Row} from "react-bootstrap";
import {NavLink} from "react-router-dom";
import {typeFixer} from "../../helpers/typeFixer";
import hash from 'object-hash';
import PopoverOverlay from "./Popover";

const PhonesRow = ({phones}) => {
    return (
        phones.length
            ?
            (
                <Row className={"mx-2"}>
                    <Col md={12} className={"mb-5"}>
                        <h2 className="h4 py-2 border-bottom mb-3">
                            Phones
                        </h2>
                        <Row>
                            <Col md={12}>
                                {
                                    phones.map(phone => (
                                        <div className="mb-2" key={hash(phone)}>
                                            <OverlayTrigger trigger={['hover', 'focus']} placement="right" data={phone} overlay={(data) => {
                                                return PopoverOverlay({...data, data:phone, pointer:"display"});
                                            }}>
                                                 <span className={"text-muted"}>
                                                    {phone.display_international}
                                                </span>
                                            </OverlayTrigger>
                                            <span className="ml-3 text-muted">
                                                { typeFixer(phone['@type']) }
                                            </span>
                                        </div>
                                    ))
                                }
                            </Col>
                        </Row>
                    </Col>
                </Row>
            )
            : null
    );
};

export default PhonesRow;