import {PERSON} from "../actions/personCreators";

const initialState = {
    personsCount: 0,
    possible_persons: [],
    person: {},
    errorHandle: false,
    isFetching: false
}

const personReducer = (state = initialState, action) => {
    const {personsCount, searchQuery} = action.payload || 0;
    switch (action.type) {
        case PERSON.SET_PERSON_INFO:
            const { person } = action.payload;
            return {
                ...state,
                person,
                personsCount,
                searchQuery,
                errorHandle: false
            }
        case PERSON.SET_PERSONS:
            const { possible_persons } = action.payload;
            return {
                ...state,
                possible_persons,
                personsCount,
                searchQuery,
                errorHandle: false
            }
        case PERSON.CLEAR_ERROR:
            return {
                ...state,
                errorHandle: false
            }
        case PERSON.SET_ERROR:
            console.log(action.status);
            return {
                ...state,
                errorHandle: action.status
            }
        case PERSON.IS_FETCHING: {
            return {
                ...state,
                isFetching: action.isFetching
            }
        }
        case PERSON.CLEAR_PERSONS: {
            return {
                ...state,
                person: {},
                possible_persons: []
            }
        }
        case PERSON.SET_QUERY: {
            const { searchQuery } = action;
            return {
                ...state,
                searchQuery
            }
        }
        default:
            return state;
    }
}

export default personReducer;