import React from 'react';
import { Popover, Row, Col } from 'react-bootstrap';
import {typeFixer} from "../../helpers/typeFixer";

const PopoverOverlay = (props) => (
    <Popover {...props} >
        <Popover.Title>
            <strong>{props.data[props.pointer] || props.data.content || props.data.names[0].display}</strong>
        </Popover.Title>
        {
            Object.keys(props.data).filter(key => {
                return key.includes('@') && !!props.data[key];
            }).length
                ?
                    (
                        <Popover.Content>
                            {
                                Object.keys(props.data).filter(key => {
                                    return key.includes('@') && !!props.data[key];
                                }).map(key => (
                                    <Row className={"mb-2"}>
                                        <Col md={5}>
                                            <strong>{ typeFixer(key) }</strong>
                                        </Col>
                                        <Col md={7} className="text-secondary">
                                            { typeFixer(props.data[key].toString()) }
                                        </Col>
                                    </Row>
                                ))
                            }
                        </Popover.Content>
                    )
                : null
        }
    </Popover>
);

export default PopoverOverlay;