import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';
import {Router} from "react-router-dom";
import {reducer as formReducer} from "redux-form";
import {applyMiddleware, combineReducers, compose, createStore} from "redux";
import {Provider} from "react-redux";
import advReducer from "./redux/reducers/advReducer";
import authReducer from './redux/reducers/authReducer';
import historyReducer from "./redux/reducers/historyReducer";
import personReducer from "./redux/reducers/personReducer";
import thunk from "redux-thunk";
import history from "./history";
import ErrorBoundary from "./components/ErrorBoundary/ErrorBoundary";
import oneLineReducer from "./redux/reducers/oneLineReducer";

const reducers = combineReducers({
    form: formReducer,
    advReducer,
    authReducer,
    historyReducer,
    personReducer,
    oneLineReducer
});

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(reducers, /* preloadedState, */ composeEnhancers(applyMiddleware(thunk)));

ReactDOM.render(
      <Provider store={store}>
          <Router history={history}>
              <ErrorBoundary>
                  <App />
              </ErrorBoundary>
          </Router>
      </Provider>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
