import {Button, Col, Dropdown, Form, Nav} from "react-bootstrap";
import * as Icon from "react-feather";
import {NavLink} from "react-router-dom";
import React from "react";
import {sendSignout} from '../../redux/actions/authCreators';
import {connect} from 'react-redux';

let ProfileMenu = ({
    sendSignout,
    data
}) => {
    return (
        <Nav className={"ml-auto order-md-2"} as={"ul"}>
            <Dropdown as={"li"} className={"nav-item py-2"}>
                <Dropdown.Toggle as={"a"} className={"nav-link d-none d-sm-block text-uppercase"}>
                    <Icon.User size={16}/>
                    <strong>{' ' + data.login}</strong>
                </Dropdown.Toggle>
                <Dropdown.Toggle bsPrefix={"no-style"} childBsPrefix={"no-style"} as={"a"} className={"nav-link d-block d-sm-none text-right"}>
                    <span className={"navbar-toggler-icon"} />
                </Dropdown.Toggle>
                <Dropdown.Menu id={"ddUserMenu"} alignRight={true}>
                    <Dropdown.Header as={"h6"} className={"text-center"}>
                        <strong>{' ' + data.email}</strong>
                    </Dropdown.Header>
                    <Dropdown.Divider />
                    <Dropdown.Item>
                        <NavLink to={"/history"}>
                            <Icon.Search size={16}/>
                            {" Search History"}
                        </NavLink>
                    </Dropdown.Item>
                    <Dropdown.Divider />
                    <Form.Row className={"mb-2"}>
                        <Col className={"mr-3 text-center"}>
                            <Button variant={"primary"} onClick={() => sendSignout()}>
                                Sign out
                            </Button>
                        </Col>
                    </Form.Row>
                </Dropdown.Menu>
            </Dropdown>
        </Nav>
    );
}

const mapStateToProps = state => {
    return {
        data: state.authReducer.data
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        sendSignout: () => dispatch(sendSignout())
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(ProfileMenu);