import {API} from "./APIConfig";

export const signIn = data => {
    return API.post(`/signin`, data);
}

export const signOut = (token) => {
    return API.get(`/signout`, {
        headers: {
            Authorization: token
        }
    });
}

export const searchOneLine = ({query, location}) => {
    const params = {};
    if(query && query.length) {
        params.query = query;
    }
    if(location && location.length) {
        params.location = location;
    }
    return API.get(`/oneLineSearch`, {
        params
    });
}

export const search = (data) => {
    return API.post(`/search`, {
        ...data,
        testMode: true
    });
}

export const topHistory = () => {
    return API.get(`/topHistory`);
}

export const getFromHistory = (searchHistoryId) => {
    return API.get(`/getFromHistory`, {
        params: {
            searchHistoryId
        }
    });
}

export const getHistory = data => {
    return API.post(`/history`, data);
}

export const getUsers = () => {
    return API.get(`/users`);
}