import React, { Component } from "react";
import {connect} from "react-redux";

class ErrorBoundary extends Component {

    constructor(props) {
        super(props);

        this.state = {
            error: null,
            errorInfo: null
        }
    }

    componentDidCatch(error, errorInfo) {
        this.setState({
            error,
            errorInfo
        })
    }

    render() {
        console.log('test');
        const {error} = this.state;
        const {authError,historyError} = this.props;
        if(error || authError || historyError) {
            return (
                <main className={"mx-3 h-100"}>
                    <div className="h-100 d-flex flex-column align-items-center justify-content-center">
                        <h1 className={"display-3 text-primary mb3"}>MGAP</h1>
                        <p className="h5 text-muted">
                            Error: {authError || historyError || error.message}
                        </p>
                    </div>
                </main>
            )
        }
        return this.props.children;
    }
}

const mapStateToProps = state => {
    return {
        authError: state.authReducer.error,
        historyError: state.historyReducer.error
    }
}


export default connect(
    mapStateToProps,
    null
)(ErrorBoundary);